@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-window {
  width: 1em;
  height: 1em;
  fill: #000;
}

@font-face {
  font-family: 'HelveticaNeue';
  src: url("../fonts/HelveticaNeue-Regular.eot");
  src: local("☺"), url("../fonts/HelveticaNeue-Regular.woff") format("woff"), url("../fonts/HelveticaNeue-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

.container {
  width: 1229px;
  margin: 0 auto;
}

.window {
  text-align: center;
  border-radius: 5px 5px 0 0;
  background-color: #e8e8e8;
  background: linear-gradient(to bottom, #f3f3f3, #d4d4d4);
  padding: 8px 0px;
}

.window:after {
  content: "";
  display: table;
  clear: both;
}

.window__circles {
  display: inline-block;
  float: left;
  margin-left: 14px;
}

.window__circles a {
  width: 10px;
  height: 10px;
  background-color: #b3b3b3;
  border-radius: 5px;
  display: inline-block;
}

.window__title {
  color: #232935;
  font-family: "HelveticaNeue", sans-serif;
  font-size: 13px;
  display: inline-block;
}

.window__icon {
  display: inline-block;
  float: right;
  margin-right: 14px;
}

.header {
  padding: 0px 18px 0px 28px;
  display: flex;
  align-items: center;
}

.header__logo {
  display: inline-block;
  margin-right: 25px;
}

.header__search {
  display: inline-block;
}

.header__settings {
  display: inline-block;
}

.logo__text {
  color: #000102;
  font-family: "Roboto", sans-serif;
  font-size: 37px;
  font-weight: 400;
}

.search__text {
  font-family: "Roboto", sans-serif;
  color: #2d3235;
  font-size: 12px;
  border: 1px solid #dcdee0;
  padding: 8px 13px;
  width: 410px;
}

.search__submit {
  border: 1px solid #dcdee0;
  cursor: pointer;
  vertical-align: middle;
  padding: 7px 8px;
  margin-left: -5px;
}

.settings__text {
  text-decoration: none;
  color: #0a4f8b;
  font-family: "Roboto", sans-serif;
  font-size: 12px;
  font-weight: 600;
  vertical-align: text-top;
}
